.auth_layout_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: var(--orange-gradient);
  overflow: hidden;
}

.auth_layout_container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: rgb(27, 27, 27);
  padding: 100px;
}
